import logo from './logo.svg';
import './App.css';
import RosterRegistPage from './component/Page/RosterRegistPage';
import { Router, Route, createBrowserRouter, createRoutesFromElements, RouterProvider } from 'react-router-dom';

const router = createBrowserRouter(
  createRoutesFromElements(
    <>
    <Route path="/regist" element={<RosterRegistPage />} />
    </>
  )
)

function App() {
  return (
    <RouterProvider router={router} />
  );
}

export default App;
