import { Alert, Box, CircularProgress, Snackbar, TextField } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { getUserData } from '../API/getData';

const classIndexList = {
  '1': '',
  '2': '',
  '3': '',
  '4': ''
}

const UserDataBox = (data) => {
  if (!data.UserId || !data.display) {
    return (<Box sx={{fontSize: '13px'}}>
      
    </Box>);
  }

  return (<Box
    sx={{
      display: 'grid',
      gridTemplateColumns: '2fr 1fr',
      alignItems: 'baseline',
      width: '100%',
    }}
  >
    <Box
      sx={{
        fontSize: '18px',
        lineHeight: '20px',
        maxWidth: '100%',
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        whiteSpace: 'nowrap',
      }}
    >
      {data.UserName}
    </Box>
    <Box
      sx={{
        fontSize: '15px'
      }}
    >
      {data.UserId}
    </Box>
    <Box
      sx={{
        fontSize: '14px',
        lineHeight: '14px',
        gridColumn: '1/3'
      }}
    >
      {`${data.grade}${
        (data.grade > 2)
          ? classIndexList[data.classNum]
          : `${data.classNum}`
      } / ${data.attendanceNum}`}
    </Box>
  </Box>)
}

const RosterCell = (props) => {
  const [pageLoaded, setpageLoaded] = useState(false);
  const [inputStudent_id, setInputStudent_id] = useState(props.value.UserId);
  const [userData, setUserData] = useState(props.value);

  const [isUserSearch, setIsUserSearch] = useState(false);

  const [saveSuccess, setSaveSuccess] = useState(false);
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const snackbarCloseHandler = () => {
    setSnackbarOpen(false);
  }

  useEffect(() => {
    if (!pageLoaded) return () => {};
    if (!inputStudent_id) return () =>{};
    let matches = inputStudent_id.match(/^[0-9]{6}$/);

    if (matches) {
      // 
      setIsUserSearch(true);

      (async () => {
        //
        let res = await getUserData(inputStudent_id, props.id);

        if (!res) setSaveSuccess(false);

        setUserData(res);
        setIsUserSearch(false);
        setSaveSuccess(true);

        // 
        setSnackbarOpen(true);
      })();
    }
  }, [inputStudent_id]);

  useEffect(() => {
    setTimeout(() => {
      setpageLoaded(true);
    }, 500);
  }, []);

  return (
    <Box
      key={props.id}
      sx={{
        display: 'grid',
        alignItems: 'stretch',
        gridTemplateColumns: '30px 180px 1fr',
        boxSizing: 'border-box',
        borderBottom: '1px solid',
        "&:last-child": {
          "borderBottom": "0"
        }
      }}
    >
      {/*  */}
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          borderRight: '1px solid'
        }}
      >
        {props.indexNumber + 1}
      </Box>

      {/*  */}
      <Box
        sx={{
          p: '4px 4px',
          borderRight: '1px solid',
        }}
      >
        <TextField
          variant="standard"
          label=""
          size='small'
          InputLabelProps={{
            shrink: true,
          }}
          InputProps={{
            sx: {
              "& input": {
                textAlign: 'center',
                padding: 0
              }
            }
          }}
          value={inputStudent_id}
          onChange={(e) => {
            setInputStudent_id(e.target.value);
          }}
          fullWidth
        />
      </Box>

      {/*  */}
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
        }}
      >
        {
          (isUserSearch)
            ? <Box
              sx={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
              }}
            >
              <CircularProgress style={{width: '27px', height: '27px'}} />
            </Box>
            : <UserDataBox {...userData} display={(inputStudent_id)? inputStudent_id.match(/^[0-9]{6}$/) : false} />
        }
      </Box>

      {/*  */}
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={2000}
        message=""
        onClose={snackbarCloseHandler}
      >
        {
          (saveSuccess)
            ? (<Alert severity="success">
                
              </Alert>)
            : (<Alert severity="error">
               
            </Alert>)
        }
      </Snackbar>
    </Box>
  )
}

export default RosterCell