import axios from "axios";
import { baseURL } from './base.js';

const getRosterData = async (data) => {
    let res = await axios.get(
        `${baseURL}/roster/getList?game_id=${data.game_id}&team_id=${data.team_id}`
    );

    return res.data;
}

const getUserData = async (user_id, roster_id) => {
    try {
        let res = await axios.get(
            `${baseURL}/roster/changeRoster?student_id=${user_id}&roster_id=${roster_id}`
        );

        return res.data;
    }catch (e) {
        return false;
    }
}

export {
    getRosterData,
    getUserData
}