import React, { useEffect, useRef, useState } from 'react';
import { getRosterData } from '../API/getData.js';
import { useLocation } from 'react-router-dom';
import { Box, Skeleton } from '@mui/material';
import RosterCell from '../Molecules/RosterCell.jsx';

const RosterRegistPage = () => {

  let searchParam = useLocation().search;
  const queryParams = new URLSearchParams(searchParam);

  const rosterData = useRef(null);
  const [teamName, setTeamName] = useState(null);
  const [sportsName, setSportsName] = useState(null);
  const [registList, setRegistList] = useState([]);

  useEffect(() => {
    // 
    (async () => {
      //
      try {
        let res = await getRosterData({
          game_id: queryParams.get('game_id'),
          team_id: queryParams.get('team_id')
        });

        rosterData.current = res;

        // 
        setSportsName(res.SportsName);
        setTeamName(res.TeamName);
        setRegistList(res.Roster);
      }catch (e) {
        // 
        window.location.href = '/error'
      }

    })();
  }, []);

  return (
    <>
    <Box
      sx={{
        maxWidth: '500px',
        m: '24px auto',
      }}
    >
      <Box key="head">
        {/*  */}
        <Box
          key="gamename"
          sx={{
            fontSize: '19px',
          }}
        >
          {
            (sportsName)
              ? `${sportsName} `
              : <Skeleton variant='rounded' width={300} height={30} />
          }
        </Box>

        <Box
          key="detail"
          sx={{
            ml: '5px',
            mt: '4px'
          }}
        >
          {/*  */}
          <Box
            sx={{
              display: 'flex',
            }}
          >
            : {
              (teamName)
                ? teamName
                : <Skeleton variant='rounded' width={130} height={20} />
            }
          </Box>
        </Box>
      </Box>

      {/*  */}
      <Box key="roster"
        sx={{
          mt: 2
        }}
      >
        {
          registList.map((elm, i) => {
            return (<Box 
              key={i}
              sx={{
                display: 'grid',
                gridTemplateColumns: '100px auto',
                alignItems: 'stretch',
                border: '1px solid',
                borderRadius: '3px',
                fontSize: '14px',
                marginBottom: '4px'
              }}
            >
              <Box
                sx={{
                  p: '4px',
                  textAlign: 'center',
                  boxSizing: 'border-box',
                  borderRight: '1px solid',
                }}
              >
                {elm.PositionName}
              </Box>
              <Box 
                key="inputs"
              >
                {
                  elm.Input.map((col, i) => {
                    return (<RosterCell
                      key={col.Id}
                      id={col.Id}
                      value={col.Value}
                      indexNumber={i}
                      isMultiple={elm.Number > 0}
                    />);
                  })
                }
              </Box>
            </Box>);
          })
        }
      </Box>
    </Box>
    </>
  )
}

export default RosterRegistPage